import React, { FC, useEffect } from 'react'
import { Button, Container, TextField, Typography } from '@material-ui/core'
import { MoonLoader } from 'react-spinners'
import { useNavigate } from 'react-router-dom'
import { yupResolver } from '@hookform/resolvers/yup'
import { useForm, Controller, SubmitHandler } from 'react-hook-form'
import useStyles from './CreateNewPassword.styles'
import FormContainer from '../../../components/Sub/FormContainer/FormContainer'
import { PROJECT_NAME } from '../../../utils/constants'
import IconSuccessRegistration from '../../../assets/Icons/IconSuccessRegistration/IconSuccessRegistration'
import { regFormNewPasswordSchema } from '../../../utils/schemas'
import processAuthServerError from '../../../utils/auth-server-error'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { createPassword } from '../../../services/axios/auth'

interface ICreateNewPasswordProps {
  token: string
}

type FormValues = {
  password: string
  password2: string
}

const CreateNewPassword: FC<ICreateNewPasswordProps> = ({ token }) => {
  const dispatch = useAppDispatch()
  const {
    createPassRequest: { status, isError, errorInfo },
  } = useAppSelector((store) => store.auth)
  const navigate = useNavigate()
  const isLoading = status === 'pending'
  const isSuccess = status === 'succeeded'
  const {
    control,
    handleSubmit,
    setError,
    formState: { errors },
  } = useForm<FormValues>({
    resolver: yupResolver(regFormNewPasswordSchema),
    mode: 'onChange',
  })

  const onSubmit: SubmitHandler<FormValues> = (data) => {
    dispatch(createPassword({ password: data.password, token }))
  }

  const onClickToMainPage = () => {
    navigate('/sign-in')
  }
  const classes = useStyles()

  useEffect(() => {
    if (isError) processAuthServerError(errorInfo, setError)
  }, [isError])

  return (
    <Container className={classes.root}>
      <FormContainer
        title={PROJECT_NAME}
        description="Придумай новый пароль"
        noTextContainer={isSuccess}
        error={errors}
      >
        {isSuccess ? (
          <div className={classes.successContainer}>
            <IconSuccessRegistration />
            <Typography variant="body1" className={classes.successTitle}>
              Пароль изменён
            </Typography>
            <Button className={classes.button} type="submit" onClick={onClickToMainPage}>
              Перейти на главную
            </Button>
          </div>
        ) : (
          <form onSubmit={handleSubmit(onSubmit)}>
            <fieldset className={classes.fieldset}>
              <Controller
                name="password"
                control={control}
                render={({ field }) => (
                  <TextField
                    {...field}
                    type="password"
                    error={!!errors.password?.message}
                    helperText={errors.password?.message || ' '}
                    className={`${classes.input} ${classes.firstInput}`}
                    variant="outlined"
                    placeholder="Придумай новый пароль"
                    inputProps={{ disableUnderline: true }}
                  />
                )}
              />
              <Controller
                name="password2"
                control={control}
                render={({ field }) => (
                  <TextField
                    {...field}
                    type="password"
                    error={!!errors.password2?.message}
                    helperText={errors.password2?.message || ' '}
                    className={classes.input}
                    variant="outlined"
                    placeholder="Повтори новый пароль"
                    inputProps={{ disableUnderline: true }}
                  />
                )}
              />

              {isLoading ? (
                <MoonLoader color="#1d6bf3" loading size={39} />
              ) : (
                <Button className={classes.button} type="submit">
                  Установить новый пароль
                </Button>
              )}
            </fieldset>
          </form>
        )}
      </FormContainer>
    </Container>
  )
}

export default React.memo(CreateNewPassword)
