import React, { FC, useEffect } from 'react'
import { MoonLoader } from 'react-spinners'
import { Button, Container, Link, TextField, Typography, CircularProgress } from '@material-ui/core'
import { Link as RouterLink, useSearchParams } from 'react-router-dom'
import { yupResolver } from '@hookform/resolvers/yup'
import { useForm, Controller, SubmitHandler } from 'react-hook-form'
import * as yup from 'yup'
import useStyles from './ResetPassword.styles'
import FormContainer from '../../../components/Sub/FormContainer/FormContainer'
import { PROJECT_NAME } from '../../../utils/constants'
import CreateNewPassword from '../CreateNewPassword/CreateNewPassword'
import processAuthServerError from '../../../utils/auth-server-error'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { resetPassword } from '../../../services/axios/auth'
import { TResetPasswordData } from '../../../services/types/auth/auth-types'

const schema = yup.object().shape({
  email: yup.string().required('Введите email').email('Такой email не подойдет'),
})
const ResetPassword: FC = () => {
  const dispatch = useAppDispatch()
  const {
    resetPassRequest: { status, isError, errorInfo },
  } = useAppSelector((store) => store.auth)
  const isLoading = status === 'pending'
  const isSuccess = status === 'succeeded'
  const [searchParams] = useSearchParams()
  const token = searchParams.get('token')
  const defaultValues = {}

  const {
    control,
    handleSubmit,
    setError,
    formState: { errors },
  } = useForm<TResetPasswordData>({
    defaultValues,
    resolver: yupResolver<yup.AnyObjectSchema>(schema),
    mode: 'onChange',
  })

  const onSubmit: SubmitHandler<TResetPasswordData> = (data) => {
    dispatch(resetPassword(data))
  }
  const classes = useStyles()

  useEffect(() => {
    if (isError) processAuthServerError(errorInfo, setError)
  }, [isError])

  return (
    <Container className={classes.root}>
      {token ? (
        <CreateNewPassword token={token} />
      ) : (
        <>
          <FormContainer
            title={PROJECT_NAME}
            subtitle="Восстановление пароля"
            description="Введи электронный адрес, указанный при регистрации"
            noTextContainer={isSuccess}
            error={errors}
          >
            {isSuccess ? (
              <Typography align="center" variant="body1" className={classes.succesText}>
                На выбранную почту были отправлены инструкции по восстановлению пароля
              </Typography>
            ) : (
              <form onSubmit={handleSubmit(onSubmit)}>
                <fieldset className={classes.fieldset}>
                  <Controller
                    name="email"
                    control={control}
                    render={({ field }) => (
                      <TextField
                        {...field}
                        error={!!errors.email?.message}
                        helperText={errors.email?.message || ' '}
                        className={classes.input}
                        variant="outlined"
                        placeholder="Почта"
                        inputProps={{ disableUnderline: true }}
                      />
                    )}
                  />

                  {isLoading ? (
                    <MoonLoader color="#1d6bf3" loading size={39} />
                  ) : (
                    <Button className={classes.button} type="submit">
                      Восстановить пароль
                    </Button>
                  )}
                </fieldset>
              </form>
            )}
          </FormContainer>
          <Typography className={classes.registerTitle} variant="body1">
            <Link className={classes.registerLink} component={RouterLink} to="/sign-in" underline="none">
              Вернуться на главную
            </Link>
          </Typography>
        </>
      )}
    </Container>
  )
}

export default React.memo(ResetPassword)
